// regular text font
@import url("https://fonts.googleapis.com/css2?family=Comic+Neue:wght@300;400;700&display=swap");

// replace Orpheus which is not available in Gfonts(font for headers) / 3 candidates for now (testing):
@import url('https://fonts.googleapis.com/css2?family=Pompiere&display=swap');
// or
// @import url("https://fonts.googleapis.com/css2?family=Amiri:wght@200;400;700&display=swap");
// or
// @import url("https://fonts.googleapis.com/css2?family=Benne:wght@200;400;600&display=swap");

html {
  font-family: $fontStackContent; // default font
  font-size: 62.5%; /* DX+UX friendly : so that 1rem = 10px... Using Rem is good for accessibility as it allows user to adjust font size with browser */
  color: $colorFontPrimary;
}

h1,h2,h3 {
  font-family: $fontStackHeaders;
  color: $colorFontAccent;
  font-weight: 200;
  margin: $spacer_S 0;
}

h1 {
  font-size: 1.3*$fontSizeSubAnnouncement; // x1.3 si Pompiere font 
  line-height: 1;
}

h3 {
  font-size: 1.6*$fontSizeContentText;
}

p {
  font-size: $fontSizeContentText;
  line-height: 1.6;
  margin: $spacer_S 0;
  @include layoutGuide(rgb(5, 140, 50));
}

em {
  font-size: $fontSizeContentText;
}

.text-center {
  text-align: center;
}

.content-text-smaller {
  font-size: $fontSizeSmallerContentText;
}
